/*=========================
  SOCIAL Icons
 ================= */

.social {
  list-style: none;
  position: fixed;
  bottom:15px;
  right: 0;
  padding-left: 0;
  z-index: 9999;
}

.social li a {
  display: block;
  height: 40px;
  width: 40px;
  background: rgb(44, 62, 80);
  border-bottom: 1px solid rgb(149, 165, 166);
  font: normal normal normal
  16px/20px 
  'FontAwesome', Helvetica, Arial, sans-serif;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  padding: 10px;
  text-decoration: none;
  text-align: center;
  transition: background .5s ease .300ms
}

.social li:last-child a { border-bottom: 0; }

.social li:first-child a:hover { background: #3b5998 }
.social li:nth-child(2) a:hover { background: #00acee }
.social li:nth-child(3) a:hover { background: #e1306c }
.social li:nth-child(4) a:hover { background: #dd4b39 }

.social li:first-child a { border-radius: 3px 0 0 0  }
.social li:last-child a { border-radius: 0 0 0 3px}




